<template>
  <div class="page-mask-demo-body">
    <v-button @click.native="showMask">Mask</v-button>
  </div>
</template>

<script>
export default {
  name: 'pageMaskDemo',
  data () {
    return {
      close () {}
    }
  },
  components: {
  },
  created () {
  },
  methods: {
    showMask () {
      this.close = this.$mask.show({
        loadingMsg: ''
      })
      setTimeout(this.showNotice, 5000)
    },
    showNotice () {
      this.$mask.pause()
      this.$notice.error({
        title: '',
        msg: '',
        link: {
          label: '',
          action: (closeNotice) => {
            this.close()
            closeNotice()
          }
        },
        duration: 0,
        onClose: this.close
      })
    }
  }
}
</script>

<style src="./pageMask.less" lang="less"></style>



