import moment from 'moment'

export const defaultOpt = {
  months: '___________'.split('_'),
  monthsShort: '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'),
  weekdays: '______'.split('_'),
  weekdaysShort: '______'.split('_'),
  weekdaysMin: '______'.split('_'),
  longDateFormat: {
    LT: 'Ahmm',
    LTS: 'Ahms',
    L: 'YYYY-MM-DD',
    LL: 'YYYYMMMD',
    LLL: 'YYYYMMMDAhmm',
    LLLL: 'YYYYMMMDddddAhmm',
    l: 'YYYY-MM-DD',
    ll: 'YYYYMMMD',
    lll: 'YYYYMMMDAhmm',
    llll: 'YYYYMMMDddddAhmm'
  },
  meridiemParse: /|||||/,
  meridiemHour: function (hour, meridiem) {
    if (hour === 12) {
      hour = 0
    }
    if (meridiem === '' || meridiem === '' || meridiem === '') {
      return hour
    } else if (meridiem === '' || meridiem === '') {
      return hour + 12
    } else {
        // ''
      return hour >= 11 ? hour : hour + 12
    }
  },
  meridiem: function (hour, minute, isLower) {
    var hm = hour * 100 + minute
    if (hm < 600) {
      return ''
    } else if (hm < 900) {
      return ''
    } else if (hm < 1130) {
      return ''
    } else if (hm < 1230) {
      return ''
    } else if (hm < 1800) {
      return ''
    } else {
      return ''
    }
  },
  calendar: {
    sameDay: function () {
      return this.minutes() === 0 ? '[]Ah[]' : '[]LT'
    },
    nextDay: function () {
      return this.minutes() === 0 ? '[]Ah[]' : '[]LT'
    },
    lastDay: function () {
      return this.minutes() === 0 ? '[]Ah[]' : '[]LT'
    },
    nextWeek: function () {
      var startOfWeek, prefix
      startOfWeek = moment().startOf('week')
      prefix = this.diff(startOfWeek, 'days') >= 7 ? '[]' : '[]'
      return this.minutes() === 0 ? prefix + 'dddAh' : prefix + 'dddAhmm'
    },
    lastWeek: function () {
      var startOfWeek, prefix
      startOfWeek = moment().startOf('week')
      prefix = this.unix() < startOfWeek.unix() ? '[]' : '[]'
      return this.minutes() === 0 ? prefix + 'dddAh' : prefix + 'dddAhmm'
    },
    sameElse: 'LL'
  },
  ordinalParse: /\d{1,2}(||)/,
  ordinal: function (number, period) {
    switch (period) {
      case 'd':
      case 'D':
      case 'DDD':
        return number + ''
      case 'M':
        return number + ''
      case 'w':
      case 'W':
        return number + ''
      default:
        return number
    }
  },
  relativeTime: {
    future: '%s',
    past: '%s',
    s: '',
    m: '1 ',
    mm: '%d ',
    h: '1 ',
    hh: '%d ',
    d: '1 ',
    dd: '%d ',
    M: '1 ',
    MM: '%d ',
    y: '1 ',
    yy: '%d '
  },
  week: {
      // GB/T 7408-1994ISO 8601:1988
    dow: 1, // Monday is the first day of the week.
    doy: 4  // The week that contains Jan 4th is the first week of the year.
  }
}

export const modified = {
  longDateFormat: {
    LT: 'HH:mm',
    LTS: 'HH:mm:ss',
    L: 'YYYY-MM-DD',
    LL: 'YYYYMMMD',
    LLL: 'YYYYMMMD HH:mm',
    LLLL: 'YYYYMMMD dddd HH:mm',
    l: 'YYYY-MM-DD',
    ll: 'YYYYMD',
    lll: 'YYYYMD HH:mm',
    llll: 'YYYYMD dddd HH:mm'
  },
  calendar: {
    sameDay: function () {
      return '[] LT'
    },
    nextDay: function () {
      return '[] LT'
    },
    lastDay: function () {
      return '[] LT'
    },
    nextWeek: function () {
      var startOfWeek, prefix
      startOfWeek = moment().startOf('week')
      prefix = this.diff(startOfWeek, 'days') >= 7 ? '[]' : '[]'
      return prefix + 'ddd HH:mm'
    },
    lastWeek: function () {
      var startOfWeek, prefix
      startOfWeek = moment().startOf('week')
      prefix = this.unix() < startOfWeek.unix() ? '[]' : '[]'
      return prefix + 'ddd HH:mm'
    },
    sameElse: 'lll'
  }
}



