<template>
  <div class="btn-demo-body">
    <v-button></v-button>
    <v-button type="primary"></v-button>
    <v-button type="ghost"></v-button>
    <v-button type="text"></v-button>
    <br/>
    <v-button type="primary" size="small"></v-button>
    <v-button type="ghost" size="small"></v-button>
    <v-button type="text" size="small"></v-button>
    <br/>
    <v-button type="primary" size="mini">mini</v-button>
    <v-button type="ghost" size="mini">mini</v-button>
    <v-button type="ghost" size="mini" shape="circle">mini</v-button>
    <v-button type="ghost" size="mini" shape="circle">
      <i class="icon-save"></i><span>mini</span>
    </v-button>
    <v-button type="text" size="mini">mini</v-button>
    <br/>
    <v-button shape="circle"></v-button>
    <v-button shape="circle" type="ghost">
      <i class="icon-save"></i><span></span>
    </v-button>
    <v-button shape="circle" type="ghost" :loading="true">
      <span></span>
    </v-button>
    <v-button shape="circle" size="small"></v-button>
    <br/>
    <v-button shape="circle" :icon="true">
      <i class="icon-save"></i>
    </v-button>
    <v-button size="small" :icon="true">
      <i class="icon-save"></i>
    </v-button>
    <v-button size="small" :icon="true" type="primary">
      <i class="icon-save"></i>
    </v-button>
    <v-button size="small" :icon="true" shape="circle">
      <i class="icon-save"></i>
    </v-button>
    <v-button size="small" :icon="true" shape="circle" type="ghost">
      <i class="icon-save"></i>
    </v-button>
    <v-button size="mini" :icon="true" shape="circle" type="ghost">
      <i class="icon-save"></i>
    </v-button>
    <v-button size="small" :icon="true" shape="circle" :loading="true">
    </v-button>
    <v-button size="small" :icon="true" shape="circle" :loading="true" type="primary">
    </v-button>
    <v-button size="small" :icon="true" shape="circle" type="text">
      <i class="icon-save"></i>
    </v-button>
    <v-button size="mini" :icon="true" shape="circle" type="text">
      <i class="icon-save"></i>
    </v-button>
    <br/>
    <v-button :disabled="true"></v-button>
    <v-button :disabled="true" type="primary"></v-button>
    <v-button :disabled="true" type="ghost"></v-button>
    <v-button :disabled="true" type="text"></v-button>
    <br/>
    <v-button :loading="btnLoading" :mini-loading-time="btnMiniLoadingTime * 1000" @click.native="clickBtn"></v-button>
    <span></span>
    <v-input-number v-model="btnMiniLoadingTime" :min="1"></v-input-number>
    <span>  </span>
    <span></span>
    <v-input-number v-model="btnLoadingTime" :min="1"></v-input-number>
    <br/>
    <v-button-group>
      <v-button type="ghost" size="small"></v-button>
      <v-button type="ghost" size="small"></v-button>
      <v-button type="ghost" size="small"></v-button>
    </v-button-group>
    <v-button-group>
      <v-button type="ghost" shape="circle"></v-button>
      <v-button type="ghost" shape="circle"></v-button>
      <v-button type="ghost" shape="circle"></v-button>
    </v-button-group>
    <v-button-group>
      <v-button type="primary" shape="circle"></v-button>
      <v-button type="success" shape="circle"></v-button>
      <v-button type="error" shape="circle"></v-button>
      <v-button type="info" shape="circle"></v-button>
      <v-button type="warning" shape="circle"></v-button>
    </v-button-group>
    <v-button-group>
      <v-button type="primary" size="small"></v-button>
      <v-button type="success" size="small"></v-button>
      <v-button type="error" size="small"></v-button>
      <v-button type="info" size="small"></v-button>
      <v-button type="warning" size="small"></v-button>
    </v-button-group>
    <v-button-group>
      <v-button type="default" size="default" :icon="true">
        <i class="icon-save"></i>
      </v-button>
      <v-button type="default" size="default"  :icon="true">
        <i class="icon-save"></i>
      </v-button>
      <v-button type="default" size="default"  :icon="true">
        <i class="icon-save"></i>
      </v-button>
    </v-button-group>
    <v-button-group :vertical="true">
      <v-button type="ghost" size="default" :icon="true">
        <i class="icon-save"></i>
      </v-button>
      <v-button type="ghost" size="default" :icon="true">
        <i class="icon-save"></i>
      </v-button>
      <v-button type="ghost" size="default" :icon="true">
        <i class="icon-save"></i>
      </v-button>
    </v-button-group>
  </div>
</template>

<script>
export default {
  name: 'btnDemo',
  data () {
    return {
      btnLoading: false,
      btnLoadingTime: 5,
      btnMiniLoadingTime: 2
    }
  },
  components: {
  },
  created () {
  },
  methods: {
    clickBtn () {
      this.btnLoading = true
      this.$notice.info({msg: ''})
      setTimeout(() => {
        this.btnLoading = false
        this.$notice.info({msg: ''})
      }, this.btnLoadingTime * 1000)
    }
  }
}
</script>

<style src="./button.less" lang="less"></style>



