<template>
  <div class="confirm-demo-body">
    <v-button @click.native="showConfirm(false)">confirm</v-button>
    <v-button @click.native="showConfirm(true)">confirm</v-button>
    <v-button @click.native="hideAllConfirm">confirm</v-button>
  </div>
</template>

<script>
export default {
  name: 'confirmDemo',
  data () {
    return {
    }
  },
  components: {
  },
  created () {
  },
  methods: {
    showConfirm (backdrop) {
      var opt = {
        backdrop: backdrop,
        title: (backdrop ? '' : '') + '',
        msg: '',
        onConfirm () {
          this.$message.success('')
        },
        onCancel () {
          this.$message.error('')
        },
        confirmText: '',
        cancelText: ''
      }
      this.$confirm.show(opt)
    },
    hideAllConfirm () {
      this.$confirm.hideAll()
    }
  }
}
</script>

<style src="./confirm.less" lang="less"></style>



