<template>
  <div class="confirm-demo-body">
    <v-button @click.native="showNotice">notice</v-button>
    <v-button @click.native="hideAllNotice">notice</v-button>
  </div>
</template>

<script>
export default {
  name: 'noticeDemo',
  data () {
    return {
    }
  },
  components: {
  },
  created () {
  },
  methods: {
    showNotice () {
      this.$notice.info({
        title: '',
        msg: '',
        duration: 0
      })
    },
    hideAllNotice () {
      this.$notice.hideAll()
    }
  }
}
</script>

<style src="./notice.less" lang="less"></style>



