<template>
  <div class="confirm-demo-body">
    <v-button @click.native="showAutoCloseMessage">message</v-button>
    <v-button @click.native="showMessage">message</v-button>
    <v-button @click.native="hideMessage">message</v-button>
    <v-button @click.native="newMessage">message</v-button>
  </div>
</template>

<script>
export default {
  name: 'messageDemo',
  data () {
    return {
    }
  },
  components: {
  },
  created () {
  },
  methods: {
    showAutoCloseMessage () {
      this.$message.info({
        msg: ''
      })
    },
    showMessage () {
      this.$message.info({
        msg: '',
        duration: 0
      })
    },
    hideMessage () {
      this.$message.hide()
    },
    newMessage () {
      this.$message.hide()
      this.$message.success({
        msg: '',
        duration: 0
      })
    }
  }
}
</script>

<style src="./message.less" lang="less"></style>



